.testimonials {
  position: relative;
  padding: 0;
  @include gradient;

  &__card {
      background-color: rgba($white, 0.8);
      padding: 2rem 1.25rem;
      border-radius: 0.625rem;
      height: auto;
      text-align: center;

      i{
          font-size: 1.5rem;
          color: rgba($primary, 0.7);
      }

      .ratings {

        i {
            color: rgba($primary, 0.7);
            font-size: 1rem;
        }

      }
  } 

  &__picture {
      width: 6rem;
      height: 6rem;
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);

      img {
        border: 0.4rem solid rgba($primary, 0.7);
      }
  }

  &__name {
     padding-top: 4rem;
     margin-bottom: 2rem;
     text-align: center;
     font-weight: 500;
     color: $white;

     h3{
       padding-top: 0.8rem;
       text-transform: capitalize;
     }

     p {
       padding-bottom: 2rem;
       text-transform: capitalize;
     }
  }
}